.step-nav {
  overflow: hidden;
  width: 100%;
  margin-left: 20px;
  margin-bottom: 20px;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  margin: 0;
  padding: 0;
  height: 35px;
  display: inline-block;
}
.step-nav li {
  float: left;
  margin: 0 .5em 0 1em;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: list-item;
  text-align: -webkit-match-parent;
}
.step-nav li span {
  background: #ddd;
  font-weight: bold;
  padding: .8em 1em;
  float: left;
  text-decoration: none;
  color: #444;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
  position: relative;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  margin: 0;
  cursor: pointer;
  margin-right: 25px;
}
.step-nav li span::before {
  content: "";
  position: absolute;
  top: 50%;
  margin-top: -1.5em;
  border-width: 1.5em 0 1.42em 1em;
  border-style: solid;
  border-color: #ddd #ddd #ddd transparent;
  left: -1em;
}
.step-nav li span::after {
  border-left-color: #ddd;
  content: "";
  position: absolute;
  top: 50%;
  margin-top: -1.5em;
  border-top: 1.5em solid transparent;
  border-bottom: 1.5em solid transparent;
  border-left: 1em solid #ddd;
  right: -1em;
}
.step-nav li span:hover {
  background: #99db76;
}
.step-nav li span:hover span::before {
  border-color: #99db76 #99db76 #99db76 transparent;
}
.step-nav li span:hover::before {
  border-color: #99db76 #99db76 #99db76 transparent;
}
.step-nav li span:hover::after {
  border-left: 1em solid #99db76;
}
.step-nav .spanActive {
  background: #99db76;
}
.step-nav .spanActive::after {
  border-left: 1em solid #99db76;
}
.step-nav .spanActive::before {
  border-color: #99db76 #99db76 #99db76 transparent;
}
